@use './colors.scss';
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');

*
{
    user-select: none;
    // outline: .5px solid red;

    &:not(svg, svg *) { color: white; }

    &::-webkit-scrollbar
    {
        background: none;
        height: 5px;
        width: 5px;
    }

    &::-webkit-scrollbar-thumb
    {
        background-color: colors.$dark70;
        border-radius: 1em;

        &:hover
        {
            cursor: pointer;
        }
    }
}

.row
{
    display: flex;
    flex-direction: row;
}

.column
{
    display: flex;
    flex-direction: column;
}

.grid { display: grid; }

body
{
    height: 100vh;
    width: 100vw;

    display: flex;
    flex-direction: row;
    
    background-color: colors.$dark25;

    font-size: 10px;
    font-family: 'Ubuntu';

    margin: 0;
    padding: 0;
}

#mainNav
{
    display: grid;
    grid-template-rows: 1fr 10fr 1fr;
    gap: 3em;

    margin: 0;
    padding: 0;
    
    height: 100vh;
    width: 15vw;
    background-color: colors.$dark15;

    .appTitle, .about
    {
        margin: 2em 0;
        align-items: center;
        justify-content: center;
        gap: 2em;
        position: relative;

        span { font-size: 2.5em; }

        &::after
        {
            content: '';
            position: absolute;
            left: 0;
            height: .25em;
            width: 100%;
            background-color: colors.$dark25;
            border-radius: 1em;
        }
    }
    .appTitle::after { bottom: -1.25em; }

    .about
    {
        &:hover { cursor: pointer; }
        &::after { top: -1.25em; }
    }

    .pageHolder
    {
        opacity: 0.75;

        .page, .subpage
        {
            position: relative;
            padding: 1em 1.5em;
            align-items: center;
            gap: 1em;

            transition:
                background-color 200ms ease,
                opacity 100ms ease;

            img
            {
                height: 2.5em;
                aspect-ratio: 1;
            }
    
            span
            {
                color: inherit;
                font-size: 1.75em;
            }
    
            &:hover
            {
                cursor: pointer;
                background-color: colors.$dark30;
            }
        }

        .subpage
        {
            display: none;
            font-size: .9em;
            padding-left: 4em;
        }

        &:has(.currentSubPage)
        {
            opacity: 1;

            .page::before
            {
                content: '';
                position: absolute;
                height: 100%;
                width: .25em;
                background-color: colors.$accent;
                left: 0;
            }

            .subpage
            {
                display: flex;

                &.currentSubPage
                {
                    color: colors.$accent;
                    
                    svg
                    {
                        fill: colors.$accent;
                    }
                }
            }
        }
    }
}

#main
{
    flex-grow: 1;
    margin: 5em;
}

.leaflet-marker-icon
{
    background: none !important;
    border: none !important;
}

.leaflet-popup-content
{
    font-family: 'Ubuntu';
    font-weight: 500;
}