@use '../colors.scss';

#trackingView
{
    flex-grow: 1;
    gap: 5%;

    .stats
    {
        $gap: 2em;

        font-size: 2em;

        height: 100%;
        width: 100%;
        gap: $gap;

        background-color: colors.$dark30;
        border-radius: .5em;
        box-shadow: 0 0 1em 0 colors.$dark15;

        overflow-y: scroll;

        .loader { display: none; }
        
        & > *
        {
            margin: 0 2em;

            &:first-child { margin-top: $gap; }
            &:last-child { margin-bottom: $gap; }

            &.title span
            {
                font-size: 1.25em;
                font-weight: 500;
            }
        }

        &.preload
        {
            & > *
            {
                display: none;
            }

            align-items: center;
            justify-content: center;

            .loader
            {
                width: 100px;
                height: 100px;
                margin: 0;
                padding: 0;
                border: 3px solid #FFF;
                border-radius: 50%;
                display: inline-block;
                position: relative;
                box-sizing: border-box;
                animation: rotation 750ms linear infinite;

                @keyframes rotation
                {
                    0% { transform: rotate(0deg); }
                    100% { transform: rotate(360deg); }
                }

                &::after
                {
                    content: '';  
                    box-sizing: border-box;
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    width: 150px;
                    height: 150px;
                    border-radius: 50%;
                    border: 3px solid;
                    border-color: colors.$accentGreen1 transparent;
                }
            }
        }

        .data
        {
            gap: 1.5em;

            .parameters
            {
                grid-template-columns: 1.5fr repeat(4, 1fr);
                justify-items: center;
                position: relative;

                &:first-child
                {
                    margin-bottom: .5em;

                    &::after
                    {
                        position: absolute;
                        content: '';
        
                        height: .15em;
                        width: 100%;
                        background-color: colors.$dark50;
        
                        bottom: -.75em;
                        left: 0;
                    }
                }

                .wifi { fill: colors.$accentGreen1; }
                .noWifi { fill: colors.$accentRed1; }
            }
        }

        .mapSettings
        {
            $gap: 2em;
            gap: calc($gap / 2);

            .title
            {
                position: relative;
                padding-left: 1em;

                &::after
                {
                    position: absolute;
                    content: '';
    
                    height: .15em;
                    width: 100%;
                    background-color: colors.$dark50;
    
                    bottom: -.75em;
                    left: 0;
                }
            }

            .options
            {
                margin-top: calc($gap / 2);
                justify-content: space-evenly;
                align-items: center;

                .bikes
                {
                    .bike
                    {
                        gap: calc($gap / 4);
                        align-items: center;
    
                        .bikeSelect
                        {
                            border: .2em solid colors.$dark60;
                            padding: 0;
                            scale: 0.75;
    
                            color: colors.$accent;
    
                            svg
                            {
                                opacity: 0;
                                transition: opacity 200ms ease;
                            }
    
                            &.checked svg { opacity: 1; }
                        }
                    }
                }
    
                .buttonWrapper
                {
                    gap: 1em;
                    width: 50%;
    
                    .traceButton, .tileButton
                    {
                        align-self: center;
                        font-size: 0.75em;
                        font-family: inherit;
                        width: 100%;
                        border: .15em solid colors.$dark60;
                        padding: .2em 0;
                    }
                }
            }
        }
    }

    #map
    {
        height: 100%;
        width: 100%;

        * { color: black; }
        
        border-radius: .5em;
        box-shadow: 0 0 1em 0 colors.$dark15;
        
        &:focus { outline: none; }
    }
}