@use '../colors.scss';

#bikeOverview
{
    padding: 4em;
    background-color: colors.$dark30;
    border-radius: .5em;
    box-shadow: 0 0 1em 0 colors.$dark15;
    gap: 2em;
    width: 100%;

    grid-template: .5fr repeat(3, 10fr) / 1fr .5fr .5fr 1fr;

    & > *
    {
        font-size: 1.75em;
        border: .1em solid colors.$dark70;
        border-radius: .5em;
        padding: 1em;

        &.title
        {
            border: 0;
            padding: 0;
            font-size: 2.5em;
            font-weight: 500;
        }

        .title
        {
            font-weight: 500;
        }

        &.span1 { grid-column: span 1; }
        &.span2 { grid-column: span 2; }
        &.span3 { grid-column: span 3; }
        &.span4 { grid-column: span 4; }
    }

    .bikeStatus
    {
        --green: #{colors.$accentGreen1};
        --yellow: #{colors.$accentYellow1};
        --red: #{colors.$accentRed1};

        gap: .25em;

        .holder
        {
            font-size: .85em;
            gap: 5em;
            flex-grow: 1;

            .legend
            {
                justify-content: space-evenly;
    
                & > *
                {
                    position: relative;
                    display: flex;
                    flex-direction: row;
                    gap: .75em;
    
                    &::before
                    {
                        content: '';
                        height: 2ch;
                        aspect-ratio: 1;
                        border-radius: .1em;
                        background-color: var(--legendColor);
                    }
                }
            }
    
            .bikes
            {
                gap: .5em;
                justify-content: space-evenly;
                
                & > .row
                {
                    align-items: center;
                    gap: .5em;
                    color: var(--legendColor);

                    &:hover { cursor: pointer; }
                }
            }
        }
    }

    .mapButton
    {
        position: relative;
        padding: 0;
        overflow: hidden;

        button
        {
            position: relative;

            padding: 0;
            margin: 0;
            display: flex;
            flex-grow: 1;
            color: white;

            * { pointer-events: none; }

            span { z-index: 2; }

            .map
            {
                height: 100%;
                width: 100%;
                z-index: 0;
            }

            @keyframes slideIn
            {
                0%
                {
                    left: 0;
                    width: 0%;
                }

                100%
                {
                    left: 0;
                    width: 100%;
                }
            }

            @keyframes slideOut
            {
                0%
                {
                    right: 0;
                    width: 100%;
                }

                100%
                {
                    right: 0;
                    width: 0%;
                }
            }

            $animationTime: 200ms;

            .text
            {
                position: absolute;
                font-size: 1.5em;
                font-weight: 500;
                font-family: 'Ubuntu';
                align-content: center;
                inset: 0;
                height: 100%;
                background-color: rgba(colors.$dark25, .75);
                transition: opacity 250ms ease;

                .actualText
                {
                    position: relative;

                    &::before
                    {
                        position: absolute;
                        content: '';
                        height: .15ch;
                        background-color: colors.$accent;
                        bottom: -.25em;
                        border-radius: 1ch;
                        animation: slideOut $animationTime ease forwards;
                    }
                }
            }

            &:hover .text .actualText::before { animation: slideIn $animationTime ease forwards; }
        }
    }
    
    .graph
    {
        gap: .5em;
        align-items: center;
        padding-bottom: 0;

        span { font-weight: 500; }

        .bar1 { color: colors.$accentGreen1; }

        .bar2 { color: colors.$accentLightBlue2; }
    }
}