@use '../colors.scss';

#override
{
    position: relative;
    padding: 4em;
    background-color: colors.$dark30;
    border-radius: .5em;
    box-shadow: 0 0 1em 0 colors.$dark15;
    gap: 5em;
    width: 100%;

    grid-template: repeat(2, 1fr) / repeat(3, 1fr);

    .outlet
    {
        font-size: 1.25rem;
        border: .1em solid colors.$dark70;
        border-radius: .5em;
        padding: 1.5em;
        gap: 1.5em;
        justify-content: center;
        align-items: center;

        .title
        {
            font-size: 1.05em;
            font-weight: bold;
        }

        .row
        {
            align-items: center;
            gap: 2em;

            .limit
            {
                width: 1cqw;
                background: none;
                border: .2em solid white;
                border-radius: .1em;
                padding: .5ch .5em;

                &:focus
                {
                    outline: none;
                }
            }
        }
    }
}