@use '../colors.scss';

#outlets
{
    position: relative;
    padding: 4em;
    background-color: colors.$dark30;
    border-radius: .5em;
    box-shadow: 0 0 1em 0 colors.$dark15;
    gap: 5em;
    width: 100%;

    grid-template: repeat(2, 1fr) / repeat(3, 1fr);

    .outlet
    {
        font-size: 1.1rem;
        border: .15em solid colors.$dark70;
        border-radius: .5em;
        padding: 1.5em;
        gap: .75em;
        transition: box-shadow 200ms ease;

        .meterData
        {
            grid-template: 1fr / 2.85fr 1fr 1fr;

            .value, .unit { text-align: right; }
        }

        .online
        {
            margin-top: auto;
            justify-content: space-evenly;

            .wifi { fill: colors.$accentGreen1; }
            .noWifi { fill: colors.$accentRed1; }
        }

        &::before
        {
            font-size: 1.05em;
            font-weight: bold;
            content: 'Outlet ' attr(data-number);
            align-self: center;
            margin-bottom: auto;
        }

        & * { pointer-events: none; }

        &:hover
        {
            cursor: pointer;
            box-shadow: 0 0 .25em .1em colors.$dark70;
        }
    }

    #modal
    {
        position: absolute;
        height: 100%;
        width: 100%;
        backdrop-filter: blur(.25em);
        opacity: 0;
        visibility: hidden;
        transition: opacity 200ms ease;

        &.visibility { visibility: visible !important; }
        &.opacity { opacity: 1 !important; }

        #moreData
        {
            font-size: 1rem;
            position: absolute;
            padding: 2em 2.5em;
            background-color: colors.$dark30;
            border: .25em solid colors.$dark70;
            border-radius: 1em;
            box-shadow: 0 0 1em .1em colors.$dark10;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            & > *:first-child
            {
                position: relative;
                align-items: center;
                justify-content: center;
                margin-bottom: 2em;

                .title
                {
                    font-size: 1.05em;
                    font-weight: bold;
                }

                button
                {
                    position: absolute;
                    right: .25em;
                    padding: 0;
                    color: colors.$accentLightBlue2;
                }

                &::after
                {
                    position: absolute;
                    content: '';
    
                    height: .15em;
                    width: 100%;
                    background-color: colors.$dark50;

                    bottom: -.75em;
                    left: 0;
                }
            }

            .data
            {
                overflow-y: scroll;
                gap: 1em;

                .meterData
                {
                    padding: 0 1em;
                    grid-template: 1fr / 5fr 1fr 1fr;
        
                    .value, .unit { text-align: right; }

                    .parameter.tab { margin-left: 2em; }
                }
            }
        }
    }
}