@use '../colors.scss';

#sessionsView
{
    flex-grow: 1;
    padding: 4em 3rem;
    background-color: colors.$dark30;
    border-radius: .5em;
    box-shadow: 0 0 1em 0 colors.$dark15;
    gap: 2em;
    overflow-y: scroll;

    .title
    {
        font-size: 2.5em;
        font-weight: 500;
    }

    .data
    {
        overflow: hidden;

        .parameters, .values
        {
            position: relative;
            font-size: 1.5em;
            grid-template-columns: repeat(5, 1fr) .5fr;
            align-items: center;
            padding: 1em;
            transition: background-color 150ms ease;

            & > *:last-child { justify-self: center; }

            &::after
            {
                position: absolute;
                content: '';
                left: 0;
                right: 0;
                bottom: 0;
                height: .25ch;
                background-color: colors.$dark50;
            }
        }

        .values
        {
            svg { color: colors.$accentLightBlue2; }

            * { pointer-events: none; }

            &:hover
            {
                cursor: pointer;
                background-color: colors.$dark40;
            }
        }
        
        .tableValues { overflow-y: scroll; }
    }
}