@use '../colors.scss';

#sessionView
{
    flex-grow: 1;
    gap: 5%;

    .stats
    {
        $gap: 1.5em;

        font-size: 2em;

        height: 100%;
        width: 100%;
        gap: $gap;

        background-color: colors.$dark30;
        border-radius: .5em;
        box-shadow: 0 0 1em 0 colors.$dark15;

        overflow-y: scroll;
        
        & > *
        {
            margin: 0 2em;

            &:first-child { margin-top: $gap; }
            &:last-child { margin-bottom: $gap; }
        }

        .details
        {
            $gap: 2em;
            gap: calc($gap / 2);

            .title
            {
                position: relative;
                gap: .5em;
                align-items: center;

                button
                {
                    --color: #{colors.$accentLightBlue2};
                    padding: .2em;
                }

                &::after
                {
                    position: absolute;
                    content: '';
    
                    height: .15em;
                    width: 100%;
                    background-color: colors.$dark50;
    
                    bottom: -.75em;
                    left: 0;
                }
            }

            .table
            {
                font-size: .8em;
                margin-top: 1em;
                grid-template-columns: 2fr 1fr;
                padding: 0 5em;
                gap: .5em;
            }
        }

        .mapSettings
        {
            $gap: 2em;
            gap: calc($gap / 2);

            .title
            {
                position: relative;
                padding-left: 1em;

                &::after
                {
                    position: absolute;
                    content: '';
    
                    height: .15em;
                    width: 100%;
                    background-color: colors.$dark50;
    
                    bottom: -.75em;
                    left: 0;
                }
            }

            .buttonWrapper
            {
                gap: 1em;
                width: 50%;
                align-self: center;
                margin-top: 1em;

                .traceButton, .tileButton
                {
                    align-self: center;
                    font-size: 0.75em;
                    font-family: inherit;
                    width: 100%;
                    border: .15em solid colors.$dark60;
                    padding: .2em 0;
                }
            }
        }
    }

    #map
    {
        height: 100%;
        width: 100%;

        span, a { color: black; }
        
        border-radius: .5em;
        box-shadow: 0 0 1em 0 colors.$dark15;
        
        &:focus { outline: none; }
    }
}